.total-cards-list{
    display: flex;
    align-items: center;
}

.total-cards-item{
    width: 25%;
    height: 90px;
    border-radius: 20px;
    display: flex;
    padding: 20px;
    margin-right: 30px;
}

.total-cards-txt{
    align-self: flex-end;
}

.total-cards-name{
    font-family: 'Lato';
    font-weight: 400;
    font-size: 14px;
    line-height: 16.8px;
}

.total-cards-count{
    font-family: 'Open Sans';
    font-weight: 700;
    font-size: 24px;
    line-height: 32.68px;
}

.total-img{
    width: 26.4px;
    height: 24px;
    align-self: flex-start;
}